.section {
  padding: 6rem 1.5rem;
  background-color: transparent;
}

.grid {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.card {
  background: rgba(30, 30, 30, 0.6);
  border: 1px solid #333;
  border-radius: 12px;
  padding: 2rem;
  cursor: pointer;
  transition: transform 0.2s, border-color 0.2s, background 0.2s;
  position: relative;
  overflow: hidden;
}

.card:hover {
  transform: translateY(-5px);
  border-color: var(--color-accent-yellow, #FFD700);
  background: rgba(40, 40, 40, 0.8);
}

.stepNumber {
  font-size: 3rem;
  font-weight: 800;
  color: rgba(255, 255, 255, 0.1);
  position: absolute;
  top: 1rem;
  right: 1.5rem;
  z-index: 0;
}

.cardContent {
  position: relative;
  z-index: 1;
}

.title {
  font-size: 1.5rem;
  font-weight: 800;
  color: #fff;
  margin-bottom: 0.5rem;
}

.subtitle {
  font-size: 1rem;
  font-weight: 600;
  color: var(--color-accent-yellow, #FFD700);
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Modal styles - largely reused but simplified */
.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 2rem;
}

.modalContent {
  background: #111;
  border: 1px solid #444;
  border-radius: 16px;
  max-width: 800px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  padding: 3rem;
  text-align: left;
}

.closeButton {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
}

.modalTitle {
  font-size: 2rem;
  color: #fff;
  margin-bottom: 0.5rem;
}

.modalSubtitle {
  font-size: 1.25rem;
  color: var(--color-accent-yellow);
  margin-bottom: 2rem;
}

.modalDescription {
  font-size: 1.125rem;
  color: #ddd;
  margin-bottom: 2rem;
}

.modalPoints {
  list-style: none;
  padding: 0;
}

.pointItem {
  margin-bottom: 1rem;
  display: flex;
  gap: 1rem;
  align-items: flex-start;
  color: #ccc;
}

.pointIcon {
  flex-shrink: 0;
  margin-top: 4px;
}