.hero {
  padding-top: 8rem;
  padding-bottom: 4rem;
  min-height: 100vh;
  display: flex;
  align-items: center;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  padding: 0 1.5rem;
}

.headline {
  font-size: 3.5rem;
  font-weight: 800;
  line-height: 1.1;
  color: #fff;
  margin-bottom: 1rem;
}

.subHeadlineMain {
  font-size: 2rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 1.5rem;
}

.description {
  font-size: 1.125rem;
  color: #ccc;
  line-height: 1.6;
  margin-bottom: 2rem;
  max-width: 90%;
}

.ctaContainer {
  display: flex;
  gap: 1rem;
}

.primaryCta {
  background: var(--color-accent-yellow);
  color: #000;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 700;
  text-decoration: none;
  transition: transform 0.2s;
}

.secondaryCta {
  background: transparent;
  color: #fff;
  padding: 0.75rem 1.5rem;
  border: 1px solid #fff;
  border-radius: 8px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: background 0.2s;
}

.primaryCta:hover,
.secondaryCta:hover {
  transform: translateY(-2px);
}

.imageWrapper {
  position: relative;
  z-index: 1;
}

.heroImage {
  width: 100%;
  height: auto;
  border-radius: 16px;
  /* box-shadow: 0 20px 40px rgba(0,0,0,0.5); */
}

@media (max-width: 768px) {
  .container {
    grid-template-columns: 1fr;
    text-align: center;
    padding-top: 6rem;
  }

  .headline {
    font-size: 2.5rem;
  }

  .ctaContainer {
    justify-content: center;
  }

  .description {
    margin: 0 auto 2rem auto;
  }
}