.footer {
  padding: 6rem 1.5rem;
  background-color: #000;
  border-top: 1px solid #333;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.brandTitle {
  font-size: 2rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
  letter-spacing: 2px;
}

.tagline {
  font-size: 1rem;
  color: #888;
  letter-spacing: 1px;
}

.poweredSection {
  margin: 1rem 0;
}

.poweredLabel {
  font-size: 0.875rem;
  color: #666;
  text-transform: uppercase;
  margin-bottom: 0.5rem;
}

.poweredName {
  font-size: 1.5rem;
  font-weight: 700;
  color: #fff;
}

.socials {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.socialLink {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  transition: all 0.2s;
}

.socialLink:hover {
  background: var(--color-accent-yellow, #FFD700);
  color: #000;
  transform: translateY(-3px);
}

.copyrightSection {
  border-top: 1px solid #222;
  padding-top: 2rem;
  margin-top: 2rem;
}

.copyright {
  font-size: 0.875rem;
  color: #555;
}